.SearchBar {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.appName {
  margin: 2rem 0 2rem 0;
  padding: 0;
  font-size: 3rem;
}

.button {
  cursor: pointer;
}

.parameters {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  width: 100%;
}

.searchInput,
.searchInput::placeholder {
  text-align: center;
  border-radius: 25px;
  height: 20px;
  width: 190px;
  font-family: "Roboto Mono", monospace;
  font-weight: bold;
  color: black;
}

.search,
.nbBooks {
  padding: 5px 0 5px 0;
}

.nbBooks {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  width: 225px;
}

.nbBooksLabel {
  margin: 0;
  padding: 0;
}

.nbBooksInput {
  width: 100%;
  padding: 0;
  margin: 5px 0 0 0;
}

h1,
label {
  color: aliceblue;
}

@media screen and (max-width: 1200px) {
  .appName {
    font-size: 2rem;
  }
}

@media screen and (max-width: 400px) {
  .appName {
    font-size: 1rem;
    margin: 1rem 0 1rem 0;
  }

  .nbBooksLabel {
    font-size: 0.8rem;
  }
  .nbBooksInput {
    width: 80%;
  }

  .searchInput,
  .searchInput::placeholder {
    font-size: 0.8rem;
    width: 170px;
    height: 18px;
  }
}
